import './css/App.css';
import { Routes, Route } from "react-router-dom";
import { useState } from "react";
import Home from "./components/Home";
import Shop from "./components/Shop";
import Product from "./components/Product";
import Nav from "./components/Nav";

function App() {  
    const [cartItems, setCartItems] = useState([]);
    const addToCart = (productid, productList) => {
    let newProduct = productList[productid];
    newProduct.quantity = 1;
    let isAdded = false
    setCartItems(prevItems => {
      prevItems.forEach(item => {
        if (item === newProduct) {
          isAdded = true;
        }
      });
      if (!isAdded) {
        return [...prevItems, newProduct];
      } else {
        return prevItems;
      }
    })
}

    return (
      <div className='main'>
        <Nav cart={cartItems} setCart = {setCartItems}/>
        <Routes>
          <Route path="/" element={<Home items={cartItems} />} />
          <Route path="/catalog" element={<Shop items={cartItems} addItems={setCartItems}/>} />
          <Route path="/catalog/:productId" element={<Product items={cartItems} addItem={addToCart}/>} />
        </Routes>
      </div>
    );
    
}

export default App;
