import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import { Link } from "react-router-dom";
import Cart from "./Cart";
const Nav = (props) => {
    const total = props.cart.reduce((add, item) => {
        const next = parseFloat(
            (Number(item.price) * Number(item.quantity)).toFixed(2)
        );
        return parseFloat(Number(add) + Number(next)).toFixed(2);
        }, 0);

    const showCart = () => {
        const cartPage = document.querySelector(".cart-main");
        cartPage.classList.add("show");
    }
    return (
        <nav className="header">
            <div className="header-inner">
                <div className="logo">
                    <h1>Timeless</h1>
                    <small>Keep Time, Look Sharp</small>
                </div>
                <ul className="routes">
                    <li className="link"><Link to="/">Home</Link></li>
                    <li className="link"><Link to="/catalog">Shop</Link></li>
                    <li className="cart" onClick={showCart}>
                        <ShoppingCartIcon sx={{ color: "#000" }} />
                        {props.cart.length > 0 ? <p className="item-count">{props.cart.length}</p> : null}
                    </li>
                </ul>
            </div>
            <Cart cart={props.cart} total={total} setCart={props.setCart} />
        </nav>
    );
};

export default Nav;