import CartCard from "./CartCard";
const uniqid = require("uniqid");
const Cart = (props) => {
    const checkout = () => {
        Notification.requestPermission().then(perm => {
            if (perm === "granted") {
                new Notification("Timeless Cart Checkout", 
                {body: `Thank you for placing an order with us. Amount payable Ksh ${props.total}`})
            }
        })
    }
    const hideCart = () => {
        const cartPage = document.querySelector(".cart-main");
        cartPage.classList.remove("show")
    }
    
    return (
        <div className="cart-main">
            <section className="cart-section">
                <h1>Your Shopping Cart</h1>
                <div className="cart-items">
                    {
                        props.cart.length > 0 ? 
                        props.cart.map((item) => {return <CartCard key={uniqid()} cart={props.cart} setCart={props.setCart} product={item}/>})
                        : <p className="no-items">Cart is Empty</p>
                    }
                </div>
                <div className="cart-actions">
                    <h3 className="total">{props.total > 0 ? `Total - Ksh: ${props.total}`: null}</h3>
                    <button onClick={checkout} className="checkout">Checkout</button>
                    <button onClick={hideCart} className="close">Close</button>
                </div>
            </section>
        </div>
    )
}

export default Cart;