const CartCard = (props) => {
    const changeQuantity = (e) => {
        e.preventDefault()
        const findIndex = props.cart.findIndex(item => item === props.product)
        props.setCart((old) => [...old], {
            [props.cart[findIndex]]: (props.cart[findIndex].quantity = e.target.value),
        });
    }
    
    return (
        <div className="item">
            <div className="item-img">
                <img src={props.product.image} alt="watch" /> 
            </div>
            <div className="item-description">
                <h3>{`${props.product.description.substring(0, 25)}...`}</h3>
                <p>Ksh {props.product.price * props.product.quantity}</p>
                <form className="change-items" onChange={changeQuantity}>
                    <h4>Quantity</h4>
                    <select className="adjust" defaultValue={props.product.quantity}>
                        <option disabled value={props.product.quantity}>{props.product.quantity}</option>
                        <option value="1">1</option>
                        <option value="2">2</option>
                        <option value="3">3</option>
                        <option value="4">4</option>
                        <option value="5">5</option>
                    </select>
                </form>
            </div>
        </div>
    )
}
export default CartCard;