import watch from "../images/watch.png";
import { Link } from "react-router-dom";

const Home = (props) => {
    return (
            <div className="landing">
                <div className="tag">
                    <p>The Time Masters</p>
                    <h3>We got all the watches, you got all the swag</h3>
                    <button><Link to="/catalog">Shop Now</Link></button>
                </div>
                <div className="landing-img">
                    <img src={watch} alt="timeless" />
                </div>
            </div>
    );
};

export default Home;